@mixin button-small {
  border-radius: $radius-small;
  font-size: 11px;
  height: 24px;
  line-height: 16px;
  padding-left: 6px;
  padding-right: 6px; }
@mixin button-medium {
  font-size: 18px;
  height: 40px;
  padding-left: 14px;
  padding-right: 14px; }
@mixin button-large {
  font-size: 22px;
  height: 48px;
  padding-left: 20px;
  padding-right: 20px; }

.button {
  @include control;
  @include unselectable;
  cursor: pointer;
  justify-content: center;
  padding-left: 10px;
  padding-right: 10px;
  text-align: center;
  white-space: nowrap;
  strong {
    color: inherit; }
  small {
    display: block;
    font-size: $size-small;
    line-height: 1;
    margin-top: 5px; }
  .icon,
  .tag {
    &:first-child {
      margin-left: -2px;
      margin-right: 4px; }
    &:last-child {
      margin-left: 4px;
      margin-right: -2px; } }
  &:hover,
  &:focus,
  &.is-active {
    color: $control-hover; }
  &:active {
    box-shadow: inset 0 1px 2px rgba($black, 0.2); }
  // Colors
  @each $name, $pair in $colors {
    $color: nth($pair, 1);
    $color-invert: nth($pair, 2);
    &.is-#{$name} {
      background-color: $color;
      border-width: 0;
      color: $color-invert;
      &:hover,
      &:focus,
      &.is-active {
        background-color: darken($color, 5%);
        border-color: transparent;
        color: $color-invert; }
      &.is-inverted {
        background-color: $color-invert;
        color: $color;
        &:hover {
          background-color: darken($color-invert, 5%); } }
      &.is-loading {
        &:after {
          border-color: transparent transparent $color-invert $color-invert !important; } }
      &.is-outlined {
        background-color: transparent;
        border-color: $color;
        border-width: 1px;
        color: $color;
        &:hover,
        &:focus {
          background-color: $color;
          border-color: $color;
          color: $color-invert; } } } }
  &.is-link {
    background-color: transparent;
    border-width: 0;
    color: $text;
    text-decoration: underline;
    &:hover,
    &:focus {
      background-color: $background;
      color: $text-strong; } }
  // Sizes
  &.is-small {
    @include button-small; }
  &.is-medium {
    @include button-medium; }
  &.is-large {
    @include button-large; }
  // Modifiers
  &[disabled],
  &.is-disabled {
    opacity: 0.5; }
  &.is-fullwidth {
    display: flex;
    width: 100%; }
  &.is-loading {
    color: transparent !important;
    pointer-events: none;
    &:after {
      @include loader;
      @include center(16px);
      position: absolute !important; } } }
